// 404

import React from 'react'
import { Link } from "react-router-dom"

const NotFound = () => {
  return (
    <>
      <h1>404</h1>
      <p></p>
      {/*    */}
      {/*    */}
      {sessionStorage.getItem("login") === "true" ? 
      <Link to='/menu'></Link> :
      <Link to='/'></Link>
    }
    </>
  )
}

export default NotFound
