// 

import React from 'react'
import { Link } from "react-router-dom"

const NotTokenAuthentication = () => {
  return (
    <>
      <h1>401</h1>
      <p></p>
      {/* login */}
      {sessionStorage.removeItem("login")}
      <Link to='/'></Link>
    </>
  )
}

export default NotTokenAuthentication
