// // (*6)
import React, { useState, useEffect } from 'react'

import axios from 'axios'
// 
import Cookies from 'universal-cookie';

import '../../css/PartyGridPokemon.css';

const PartyGridPokemon4 = (props: any) => {
    // DB
    const [pokemonValueList, setPokemonValueList] = useState<any>([]);
    // cookie
    const cookies = new Cookies();

    // 
    useEffect(() => {
        getPokemonList()
    },[])
        
    // DB
    // GET"option"
    const getPokemonList = () => {
      // axios.get()APIURLlocalhost:8000/v1/~
      axios.get(process.env.REACT_APP_API_URL + 'v1/party_grid/', {
        params: {
            // API
            pokemon_name4: (props.currentSelectPokemon4),
            index: props.index,
        },
        // headercookie
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `JWT ${cookies.get('accesstoken')}`
        }
      })
      .then(response=>{
            if (props.index === 3) {
                const keyLst = [];
                const valueLst = [];
                for (const [key, value] of Object.entries(response.data.records[0])) {
                  keyLst.push(key);
                  valueLst.push(value);
                }
                setPokemonValueList(valueLst);
            }
        })
        .catch(error=>{
            window.console.error(`axios-FAILED:${error}`);
        })
    }

  return (
      <div className='total_container'>
        <div className='key_container'>
          <div>
            <p>ID</p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p>HP</p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p>HP</p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
          </div>
        </div>
        <div className='value_container'>
          {pokemonValueList.map((row: any) => (
            <p>{row === null ? "-" : row}</p>
          ))}
        </div>
      </div>
  )
}

export default PartyGridPokemon4
